// boilerplate optional components
.c-stsSend {
    @keyframes animationRotateLoader {
        from {@include transforms(rotate(0deg), center)}
        to {@include transforms(rotate(360deg), center)}
    }
    position: relative;
    overflow: hidden;
    width: 100%;
    &__feedback {
        @include roundedAll($radius: 5px);
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        line-height: 28px;
        padding: 20px;
        &.--error {
            background: $feedbackError;
            span {
                color: $white;
            }
        }
        &.--loading, &.--load {
            background: $feedbackLoad;
            span {
                color: $text;
            }
            i {
                animation-name: animationRotateLoader;
                animation-duration: 1s;
                animation-iteration-count: infinite;
                animation-timing-function: linear;
            }
        }
        &.--success {
            background: $feedbackSuccess ;
            span {
                color: $white;
            }
        }
        
        i {
            @include flexWidth(40px);
            max-height: 40px;
            svg {
                width: 40px;
                height: 40px;
            }
        }
        
        span {
            @include letterSpacing($psValue: 50);
            display: block;
            padding-left: 15px;
            font-size: 16px;
            line-height: 1.2;
        }
    }
}
.c-simpleForm {
    display: block;
    ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        @include calcWidthForPositionNegative(-15px);
    }
    li {
        position: relative;
        display: block;
        padding: 15px;
    }
    li.liBtn {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        .field--hiddenValidate, input {
            position: absolute;
            bottom: 0;
            right: 0;
            display: inline;
            width: 0;
            height: 0;
            @include opacity(0);
        }
    }
    li.liFile {
        .inputText {
            position: relative;
            z-index: 1;
            cursor: pointer;
            padding-right: 50px;
        }
        .inputAttach {
            cursor: pointer;
            background: $white;
            position: absolute;
            z-index: 2;
            left: 15px;
            bottom: -5px;
            right: 0;
            width: 100%;
            @include calc(width, 100%, '-', 30px);
            @include opacity(0);
        }
        &:after {
            @include transforms(rotate(0deg), center);
            @include transition();
            content: '';
            width: 20px;
            height: 20px;
            display: block;
            position: absolute;
            bottom: 26px;
            right: 35px;
            background: url(#{$media_icons}icon_attach.svg);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            z-index: 1;
        }
    }
    li.liSelect {
        > div {
            display: block;
            width: 100%;
            position: relative;
            &:after {
                @include transforms(rotate(0deg) translateY(50%), center);
                @include transition();
                content: '';
                width: 12px;
                height: 10px;
                display: block;
                position: absolute;
                bottom: 50%;
                right: 20px;
                background: url(#{$media_assets}caret-down.svg);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: contain;
            }
        }
        &.is-active {
            > div {
                &:after {
                    @include transforms(rotate(180deg) translateY(50%), bottom);
                }
            }
        }
    }
    li.liCheckbox {
        div {
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-wrap: nowrap;
            margin-bottom: 10px;
            &:last-of-type {
                margin-bottom: 0;
            }
            * {
                cursor: pointer;
            }
            label {
                @include paragrafos();
                text-transform: none;
                order: 2;
                padding-left: 10px;
                margin-bottom: 0;
            }
            input {
                @include roundedAll(4px);
                @include flexWidth(24px);
                display: block;
                padding: 0;
                height: 24px;
                &:checked {
                    background: url(#{$media_icons}/icon_checked.svg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                }
            }
        }
    }
    li.liRadio {
        div {
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-wrap: nowrap;
            margin-bottom: 10px;
            &:last-of-type {
                margin-bottom: 0;
            }
            * {
                cursor: pointer;
            }
            label {
                @include paragrafos();
                text-transform: none;
                order: 2;
                padding-left: 10px;
                margin-bottom: 0;
            }
            input {
                @include roundedAll(12px);
                @include flexWidth(24px);
                display: block;
                padding: 0;
                height: 24px;
                &:checked {
                    background: url(#{$media_icons}/icon_checked.svg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                }
            }
        }
    }
    label, input, select, textarea, .listOfSelectedJobs {
        @include transition();
        display: block;
        width: 100%;
    }
    input, select, textarea, .listOfSelectedJobs {
        background: $white;
        border: 1px solid $lightGray;
    }
    .listOfSelectedJobs {
        span {
            @include paragrafos($tc: $placeholderText);
        }
        span[data-codejob] {
            @include paragrafos();
        }
    }
    input, select, textarea {
        @include paragrafos;
        @include placeholder {
            @include paragrafos($tc: $placeholderText);
        }
        &.error{
            border: 1px solid $feedbackError;
        }
        &:focus {
            border-color: $primary;
        }
    }
    input, select {
        height: 44px;
        line-height: 44px;
        padding: 0 20px;
    }
    select {
        &:invalid {
            color: $placeholderText;
        }
    }
    textarea {
        padding: 15px 20px;
        line-height: 1.5;
        height: 100px;
        max-height: 200px;
        min-height: 100px;
        resize: vertical;
    }
    label {
        display: block;
        margin-bottom: 5px;
        font-size: 14px;
        &.error{
            font-size: 12px;
            color: $feedbackError;
            bottom: -5px;
            left: 15px;
            position: absolute;
        }
    }
}

// buttons / links
.c-btn {
    $c_btn_heightDefault: 58px;
    $c_btn_heightSmall: 52px;
    $c_btn_maxWidth: 260px;
    
    @include transition();
    @include font($fw: $fwBold);
    position: relative;
    overflow: hidden;
    display: block;
    min-width: 130px;
    max-width: $c_btn_maxWidth;
    width: 100%;
    cursor: pointer;
    background: $primary;
    height: $c_btn_heightDefault;
    line-height: $c_btn_heightDefault;
    text-align: center;
    text-transform: uppercase;
    font-size: 22px;
    color: $white;
    white-space: nowrap;
    // default btns
    &.--primary {
        background: $primary;
        color: $white;
        &:hover {
            background: $primaryDark;
        }
    }
    &.--secondary {
        background: $secondary;
        color: $white;
        &:hover {
            background: $secondaryDark;
        }
    }
    &.--yellow {
        background: $yellow;
        color: $white;
        &:hover {
            background: $yellowDark;
        }
    }
    &.--blue {
        background: $blue;
        color: $white;
        &:hover {
            background: $blueDark;
        }
    }
    &.--green {
        background: $green;
        color: $text;
        &:hover {
            background: $greenDark;
        }
    }
    &.--disabled {
        background: $lightGray;
        color: $baseGray;
        pointer-events: none;
    }

    // stroke btns
    &.--stroke {
        border: 2px solid;
        line-height: $c_btn_heightDefault - 4;
        &.--primary {
            background: transparent;
            color: $primary;
            border-color: $primary;
            &:hover {
                background: $primaryDark;
                border-color: $primaryDark;
                color: $white;
            }
        }
        &.--secondary {
            background: transparent;
            color: $secondary;
            border-color: $secondary;
            &:hover {
                background: $secondaryDark;
                border-color: $secondaryDark;
                color: $white;
            }
        }
        &.--yellow {
            background: transparent;
            color: $yellow;
            border-color: $yellow;
            &:hover {
                background: $yellowDark;
                border-color: $yellowDark;
                color: $white;
            }
        }
        &.--green {
            background: transparent;
            color: $green;
            border-color: $green;
            &:hover {
                background: $greenDark;
                border-color: $greenDark;
                color: $white;
            }
        }
        &.--blue {
            background: transparent;
            color: $blue;
            border-color: $blue;
            &:hover {
                background: $blueDark;
                border-color: $blueDark;
                color: $white;
            }
        }
        &.--disabled {
            background: transparent;
            color: $mediumGray;
            border-color: $mediumGray;
            pointer-events: none;
        }
    }
    // small
    &.--small {
        &.--primary,
        &.--secondary,
        &.--yellow,
        &.--blue,
        &.--green,
        &.--disabled {
            height: $c_btn_heightSmall;
            line-height: $c_btn_heightSmall;
            font-size: 18px;
        }
    }
    // small with stroke
    &.--small {
        &.--stroke {
            &.--primary,
            &.--secondary,
            &.--yellow,
            &.--blue,
            &.--green,
            &.--disabled {
                line-height: $c_btn_heightSmall - 4;
                font-size: 18px;
            }
        }
    }
}
