// lists
.c-infosContato {
    display: block;
    li {
        display: block;
        margin-bottom: 20px;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    a {
        display: flex;
        flex-direction: row;
        align-items: center;
        i {
            @include flexWidth(40px);
            padding-right: 10px;
            svg {
                width: 30px;
                height: 30px;
                display: block;
                path {
                    @include transition();
                    fill: $primary;
                }
            }
        }
        .caption {
            @include transition();
            @include paragrafos();
            font-style: normal;
        }
        &:hover {
            i {
                svg path {
                    fill: $secondary;
                }
            }
            .caption {
                color: $secondary;
            }
        }
    }
}
.c-socialList {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    li {
        padding: 3px;
    }
    a {
        display: block;
    }
    svg {
        display: block;
        width: 20px;
        height: 20px;
        path, circle {
            @include transition();
        }
    }
    &.--rounded {
        a {
            @include transition();
            @include roundedAll(50%);
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            width: 38px;
            height: 38px;
        }
        &.--primary {
            a {
                background: $primary;
                svg path, svg circle {
                    fill: $white !important;
                }
                &:hover {
                    background: $primaryDark;
                }
            }
        }
        &.--secondary {
            a {
                background: $secondary;
                svg path, svg circle {
                    fill: $white !important;
                }
                &:hover {
                    background: $secondaryDark;
                }
            }
        }
        &.--white {
            a {
                background: $white;
                svg path, svg circle {
                    fill: $baseGray !important;
                }
                &:hover {
                    background: $white3;
                }
            }
        }
    }
    &.--default {
        a {
            @include transition();
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            width: 28px;
            height: 28px;
        }
        &.--primary {
            a {
                svg path, svg circle {
                    fill: $primary !important;
                }
                &:hover {
                    svg path, svg circle {
                        fill: $primaryDark !important;
                    }
                }
            }
        }
        &.--secondary {
            a {
                svg path, svg circle {
                    fill: $secondary !important;
                }
                &:hover {
                    svg path, svg circle {
                        fill: $secondaryDark !important;
                    }
                }
            }
        }
        &.--white {
            a {
                svg path, svg circle {
                    fill: $white !important;
                }
                &:hover {
                    svg path, svg circle {
                        fill: $white3 !important;
                    }
                }
            }
        }
    }
}

// grid e tabs
.c-responsiveGrid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    //li {
    //    background: $lightGray;
    //    padding: 10px;
    //    &:nth-of-type(odd) {
    //        background: $white2;
    //    }
    //}
    &.--col12 {li {@extend .u-fw8;}}
    &.--col11 {li {@extend .u-fw9;}}
    &.--col10 {li {@extend .u-fw10;}}
    &.--col9 {li {@extend .u-fw11;}}
    &.--col8 {li {@extend .u-fw12;}}
    &.--col7 {li {@extend .u-fw14;}}
    &.--col6 {li {@extend .u-fw16;}}
    &.--col5 {li {@extend .u-fw20;}}
    &.--col4 {li {@extend .u-fw25;}}
    &.--col3 {li {@extend .u-fw33;}}
    &.--col2 {li {@extend .u-fw50;}}
