.c-tabHorizontal {
    display: block;
    width: $containerWidth;
    max-width: 100%;
    overflow: hidden;
    &__header {
       display: flex;
       flex-direction: row;
       align-items: stretch;
       justify-content: space-between;
       flex-wrap: nowrap;
       background: $white;
       ul {
           @include scroll($size: 5px);
           @include transforms(rotate(180deg), center);
           background: $white;
           direction: rtl;
           flex: 1 1 auto;
           overflow: auto;
           white-space: nowrap;
           display: flex;
           flex-direction: row;
           align-items: center;
           justify-content: flex-start;
       }
       li {
           @include transforms(rotate(180deg), center);
           display: block;
           position: relative;
           padding-right: 56px;
           &:after {
               content: '';
               display: block;
               width: 1px;
               height: 100%;
               position: absolute;
               top: 0;
               right: 26px;
               background: $primaryLight;
           }
           &:last-of-type {
               padding-right: 0;
               &:after {
                   display: none;
               }
           }
       }
       a {
           @include font(Gotham, bold);
           font-size: 40px;
           background: $white;
           display: block;
           color: $lightGray;
           text-align: center;
           padding: 0;
           text-decoration: none;
           &:hover {
               color: $primaryLight;
           }
           &.is-active {
               color: $primary;
           }
       }
       &.is-scroll {
           .scrollBtn {
               display: flex;
               flex-direction: row;
               align-items: center;
               justify-content: center;
           }
       }
   }
    &__content {
        @include transition();
        @include u-hideElement;
        display: block;
        padding: 20px 0 0 0;
        &.is-active {
            @include u-showElement;
        }
        p, li {
            @include font(Gotham, bold);
            font-size: 20px;
            line-height: 34px;
            color: $text;
        }
    }
}

// project components
.c-search {
    width: 220px;
    max-width: 100%;
    display: block;
    position: relative;
    input {
        @include font(Gotham, bold);
        @include transition();
        display: block;
        width: 100%;
        height: 40px;
        line-height: 40px;
        padding: 0 40px 0 0;
        text-align: right;
        border-bottom: 4px solid $primary;
        text-transform: uppercase;
        color: $yellow;
        font-size: 11px;
        @include placeholder {
            color: $yellow;
        }
        &:focus {
            border-bottom: 4px solid $yellow;
        }
    }
    button {
        @include transition();
        @include roundedAll(50%);
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        position: absolute;
        right: 0;
        top: 5px;
        background: transparent;
        cursor: pointer;
        &:hover {
            background: $white2;
        }
    }
}
.c-cardDepartment {
    display: block;
    width: 100%;
    figure {
        @extend .u-aspectRatio;
        padding-top: 115%;
        img {
            @include transition();
        }
    }
    div {
        display: block;
    }
    .c-btn {
        display: block;
        width: 100%;
        max-width: 140px;
        height: 35px !important;
        line-height: 35px !important;
        font-size: 11px !important;
        margin: -17px auto 0 auto;
    }
    &:hover {
        figure {
            img {
                @include transforms(scale(1.1), center);
            }
        }
    }
}
.c-cardProduct {
    @include transition();
    &__pic {
        @extend .u-aspectRatio;
        padding-top: 138%;
        img {
            @include transition();
        }
        &:hover {
            img {
                @include transforms(scale(1.1), center);
            }
        }
    }
    &__info {
        padding: 0 20px;
    }
    .infos__resume {
        padding-top: 40px;
        padding-bottom: 40px;
        h1 {
            @include font(Gotham, bold);
            display: block;
            font-size: 36px;
            line-height: 1;
            color: $secondary;
            margin-bottom: 25px;
            &:hover {
                color: $secondaryDark;
            }
        }
        span {
            @include font(Gotham, bold);
            display: block;
            width: 100%;
            line-height: 1;
        }
        .code {
            @include font(Gotham, bold);
            display: block;
            margin-bottom: 5px;
            font-size: 13px;
        }
        .price__old {
            color: $blue;
            font-size: 23px;
        }
        .price__now {
            color: $primary;
            font-size: 38px;
        }
        .price__time {
            color: $primary;
            font-size: 19px;
        }
    }
    .infos__buy {
        .c-btn {
            max-width: 100%;
            text-transform: none;
            font-size: 20px;
        }
        .securePay {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            margin-top: 15px;
            img {
                display: block;
                width: 11px;
            }
            span {
                @include font(Gotham, bold);
                font-size: 13px;
                line-height: 11px;
                color: $text;
                padding-left: 8px;
            }
        }
    }
    .infos__buy__unavailable {
        input {
            @include font(Gotham, bold);
            @include transition();
            display: block;
            width: 100%;
            border: 1px solid rgba(0,0,0,0.25);
            height: 70px;
            line-height: 70px;
            text-align: center;
            background-color: transparent;
            margin-bottom: 10px;
            font-size: 14px;
            color: $text;
            @include placeholder {
                @include font(Gotham, bold);
                color: $text;
                font-size: 18px;
                text-transform: uppercase;
            }
            &:focus {
                border: 1px solid #242424;
            }
        }
        .c-btn {
            max-width: 100%;
            text-transform: uppercase;
            font-size: 18px;
            height: 70px;
            line-height: 70px;
        }
    }
    .infos__instagram {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        padding-bottom: 20px;
        border-bottom: 1px solid $primary;
        i {
            @include flexWidth(33px);
            @include roundedAll(10px);
            display: flex;
            align-items: center;
            justify-content: center;
            height: 33px;
            border: 2px solid #0A000F;
            svg {
                display: block;
                width: 18px;
                height: 18px;
                path, circle {
                    fill: #0A000F;
                }
            }
        }
        div {
            padding-left: 25px;
            strong, p {
                @include font(Gotham, bold);
            }
            strong {
                color: $secondary;
                display: block;
                font-size: 15px;
            }
            p {
                font-size: 13px;
                color: $text;
            }
        }
    }
    .infos__variants {
        padding-top: 20px;
        border-top: 1px solid $primary;
        padding-bottom: 25px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
        .variants__colors {
            flex: 0 1 auto;
            padding-right: 20px;
            margin-bottom: 25px;
            div {
                display: flex;
                flex-direction: row;
                align-items: center;
                flex-wrap: wrap;
            }
            input {
                @include roundedAll(0);
                border: 1px solid $white3;
                width: 17px;
                height: 44px;
                display: block;
                margin-right: 6px;
                cursor: pointer;
                &:last-of-type {
                    margin-right: 0;
                }
                &:checked {
                    border: 2px solid $primary;
                }
            }
        }
        .variants__qtf {
            @include flexWidth(95px);
            margin-bottom: 25px;
            input {
                @include font(Gotham, bold);
                height: 44px;
                width: 100%;
                display: block;
                background: $blue;
                font-size: 20px;
                text-align: center;
            }
        }
        .variants__sizes {
            @include flexWidth(100%);
            a {
                display: flex;
                flex-direction: row;
                align-items: center;
                img {
                    display: block;
                    width: 19px;
                }
                span {
                    @include transition();
                    @include font(Gotham, bold);
                    font-size: 13px;
                    line-height: 11px;
                    color: $text;
                    padding-left: 8px;
                }
                &:hover {
                    span {
                        color: $primary;
                    }
                }
            }
        }
        .label {
            @include font(Gotham, bold);
            display: block;
            font-size: 15px;
            color: $blue;
            margin-bottom: 10px;
        }
    }
}
.c-cardProductInCart {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    .info__title {
        @include font(Gotham, bold);
        padding-right: 35px;
        font-size: 16px;
    }
    .info__values {
        div {
            text-align: right;
            display: block;
            width: 100%;
        }
        .value__price {
            @include font(Gotham, bold);
            font-size: 16px;
        }
        .value__qtd {
            @include font(Gotham, normal);
            font-size: 14px;
        }
    }
}