// l-nav - primary
@media screen and (max-width: 1170px) {
    .l-nav {
        &__primary {
            div, ul {
                @include flexWidth(100%);
                text-align: center;
            }
        }
    }
}
@media screen and (max-width: 940px) {
    .l-nav {
        .c-hamburguer {
            display: block;
            &.is-active {
                margin-top: -40px;
            }
        }
    }
}
@media screen and (max-width: 720px) {
    .l-nav {
        &__primary {
            display: none;
        }
        .c-hamburguer {
            display: block;
            &.is-active {
                margin-top: -10px;
            }
        }
    }
}

// .l-nav - secondary / third
@media screen and (max-width: 940px) {
    .l-nav {
        &__secondary {
            padding: 20px 0;
            .u-container {
                justify-content: flex-start;
            }
        }
        &__search {
            display: none;
        }
        &__userControl {
            right: 65px;
        }

        &__contentHero {
            @include transition();
            display: block;
            background: $primary;
            height: 100vh;
            overflow: auto;
            position: fixed;
            right: -300px;
            top: 0;
            width: 280px;
            max-width: 100%;
            padding-bottom: 50px;
            padding-top: 80px;
            &.is-active {
                right: 0;
            }
            .u-dnone {
                display: block !important;
            }
        }
        &__menu {
            border-top: 1px solid $primaryDark;
            border-bottom: 1px solid $primaryDark;
            padding: 10px 0;
            > li {
                width: 100%;
                display: block;
                text-align: right;
                margin-right: 0;
            }
            .c-btn {
                text-align: right;
                max-width: 100%;
            }
        }
        &__third {
            .l-nav__search {
                @include flexWidth(100%);
                display: block;
                position: relative;
                padding: 0 15px;
                .c-search {
                    width: 100%;
                }
            }
            .l-nav__userControl {
                @include flexWidth(100%);
                position: relative;
                right: 0;
                padding: 15px;
                border-top: 1px solid $primaryDark;
                margin-top: 15px;
                justify-content: flex-end;
                flex-wrap: wrap;
                .s-cart {
                    @include flexWidth(100%);
                    position: relative;
                    overflow: hidden;
                    padding: 0 20px;
                    max-height: 0;
                    &.is-active {
                        padding: 20px;
                        max-height: inherit;
                        top: 0;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 540px) {
    .l-nav__brand {
        @include flexWidth(150px);
    }
    .l-nav__userControl {
        display: none;
    }
    .l-nav__third .l-nav__userControl {
        display: flex ;
    }
}

// l-footer
@media screen and (max-width: 950px) {
    .l-footer {
        padding: 60px 0 70px 0;
        .u-container {
            justify-content: center;
            left: 0;
            width: 100%;
            > * {
                margin-bottom: 20px;
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }
        &__brand {
            @include flexWidth(100%);
            a {
                max-width: 200px;
                margin: 0 auto;
            }
        }
        &__menu {
            @include flexWidth(240px);
            a {
                display: block;
                width: 100%;
                padding: 10px 0;
                text-align: center;
            }
        }
        &__PSmethods {
            @include flexWidth(100%);
            .img__blendMode {
                margin: 0 auto;
            }
        }
        &__newsletter {
            h4, p {
                text-align: center;
            }
        }
    }
}

// l-copyright
@media screen and (max-width: 780px) {
    .l-copyright {
        justify-content: center;
        &__legal {
            @include flexWidth(100%);
            padding-right: 0;
            margin-bottom: 30px;
            p {
                text-align: center;
            }
        }
    }
}

// l-headerHome
@media screen and (max-width: 1020px) {
    .l-headerHome__content {
        @include transforms(translateY(-50%), center);
        padding-top: 0;
    }
}
@media screen and (max-width: 940px) {
    .l-headerHome {
        padding-top: 190px;
    }
}
@media screen and (max-width: 780px) {
    .l-headerHome {
        .u-containerFlex {
            flex-wrap: wrap;
            max-width: 640px;
        }
        &__figureOne,
        &__figureTwo {
            @include flexWidth(100%);
        }

        &__figureOne {
            img {
                left: 0;
            }
        }
        &__figureTwo {
            margin-top: -80px;
            z-index: 3;
            img {
                z-index: 2;
                left: -20px;
            }
        }

        &__content {
            h1 {
                font-size: 48px;
                padding: 0 50px;
            }
        }
    }
}
@media screen and (max-width: 720px) {
    .l-headerHome {
        padding-top: 150px;
    }
}

// l-innerPage
@media screen and (max-width: 940px) {
    .l-innerPage {
        padding-top: 140px;
    }
}
@media screen and (max-width: 720px) {
    .l-innerPage {
        padding-top: 100px;
    }
}

// s-departmentIntro
@media screen and (max-width: 540px) {
    .s-departmentIntro h1 {
        font-size: 30px;
        text-align: center;
    }
}

// s-departmentFilter
@media screen and (max-width: 1080px) {
    .s-departmentProducts__filters {
        .u-containerFlex {
            justify-content: space-between;
            flex-wrap: wrap;
        }
        .filter {
            @include flexWidth(50%);
            padding: 10px 20px;
            margin-right: 0;
            justify-content: space-between;
            .u-subMenu {
                right: 20px;
            }
            &.--order {
                justify-content: space-between;
            }
            &.is-active {
                .u-subMenu {
                    top: 40px;
                }
            }
        }
    }
}
@media screen and (max-width: 780px) {
    .s-departmentProducts__filters {
        .filter {
            @include flexWidth(100%);
            padding-left: 0;
            padding-right: 0;
            border-bottom: 1px solid $mutedGray;
            &:last-of-type {
                border-bottom: 0;
            }
            .u-subMenu {
                right: 0;
            }
        }
    }
}

// s-styleProfile
@media screen and (max-width: 680px) {
    .s-styleProfile {
        &__banner {
            justify-content: center;
        }
        .banner__text {
            padding-left: 0;
            margin: 0 auto;
            left: 0;
            right: 0;
            p, h2, img {
                margin-left: auto;
                margin-right: auto;
                text-align: center;
            }
            img {
                max-width: 230px;
            }
        }
    }
}

// s-mainProduct
@media screen and (max-width: 1260px) {
    .s-mainProduct__media {
        .media__text {
            font-size: 18px;
        }
    }
}
@media screen and (max-width: 1080px) {
    .s-mainProduct__media {
        flex-wrap: wrap;
        .media__pic,
        .media__text,
        .media__video {
            @include flexWidth(100%);
        }
        .media__text {
            @include transforms(rotate(0deg), center);
            padding: 30px 0;
            writing-mode: inherit;
            white-space: normal;
        }
        .media__pic,
        .media__video {
            a {
                padding-top: 0;
                img {
                    position: relative;
                }
            }
        }
    }
}
@media screen and (max-width: 800px) {
    .s-mainProduct__infos {
        @include flexWidth(320px);
        padding-left: 30px;
    }
}
@media screen and (max-width: 615px) {
    .s-mainProduct {
        .u-containerFlex {
            flex-wrap: wrap-reverse;
        }
    }
    .s-mainProduct__infos {
        @include flexWidth(100%);
        padding-left: 0;
        margin-bottom: 20px;
        .c-cardProduct {
            position: relative;
            top: 0 !important;
            margin-bottom: 40px;
            .infos__instagram {
                display: none;
            }
            .infos__resume {
                padding-top: 0;
            }
        }
    }
    .s-mainProduct__media {
        @include flexWidth(100%);
    }
}

// s-buyMore
@media screen and (max-width: 1150px) {
    .s-buyMore ul {
        li.card {
            @include flexWidth(220px);
        }
        li.spacer {
            @include flexWidth(100px);
            span {
                font-size: 80px;
            }
        }
    }
}
@media screen and (max-width: 1150px) {
    .s-buyMore ul {
        li.card {
            @include flexWidth(200px);
        }
        li.spacer {
            @include flexWidth(60px);
            span {
                font-size: 40px;
            }
        }
    }
}
@media screen and (max-width: 760px) {
    .s-buyMore {
        display: none;
    }
}

// s-buyFullCover
@media screen and (max-width: 1000px) {
    .s-buyFullCover__hero .fixedBuy {
        @include flexWidth(340px);
    }
    .s-buyFullCover .c-cardProduct {
        padding: 20px;
    }
}
@media screen and (max-width: 800px) {
    .s-buyFullCover__hero .fixedBuy {
        position: relative;
        @include flexWidth(100%);
        margin-top: 40px;
    }
    .s-buyFullCover .c-cardProduct {
        padding: 0;
        position: relative;
        top: 0 !important;
        .infos__variants {
            display: none;
        }
    }
}

