// Layout Colors
// oranges
$primaryLight: #feb093;
$primary: #fe9872;
$primaryDark: #df744c;

// pinks
$secondaryLight: #ff9dc0;
$secondary: #f885ae;
$secondaryDark: #e56895;

// yellows
$yellowLight: #ffd463;
$yellow: #ffc327;
$yellowDark: #efb51e;

// blues
$blueLight: #d9ebfd;
$blue: #c2dbf1;
$blueDark: #a8cae7;

// greens
$greenLight: #d2ffc1;
$green: #bcfea4;
$greenDark: #abf590;


$white: #fff;
$white2: #f8f8f8;
$white3: #f2f2f2;

$mutedGray: #ededed;
$lightGray: #DADADA;;
$mediumGray: #c5c5c5;
$darkGray: #4c4c4c;
$baseGray: #898989;

$defaultRadius: 5px;

$feedbackSuccess: $secondaryDark;
$feedbackLoad: $secondary;
$feedbackError: #c03a4d;

$whatsappPrimary: #25d366;
$whatsappSecondary: #1eb456;

// Sizes
$containerWidth: 1290px;
$containerBigWidth: 1440px;
