$media_content : "../media/img/content/";
//example: background: url(#{$media_assets}image.png);

// text
@import '../partials/fontFace';

$text: #000000;
$placeholderText: lighten($text, 15%);
$fontFamily__default: 'Gotham', 'Roboto', 'Arial', 'Calibri', sans-serif;
$fontFamily__roboto: 'Roboto', 'Arial', 'Calibri', sans-serif;
$fontFamily__serif: 'American Typewriter', serif;
$fwRegular: normal;
$fwMedium: 500;
$fwBold: bold;