@font-face {
	font-family: 'Roboto';
	src: url('../media/type/Roboto-Medium.woff2') format('woff2'),
	url('../media/type/Roboto-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Gotham';
	src: url('../media/type/Gotham-Medium.woff2') format('woff2'),
	url('../media/type/Gotham-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Gotham';
	src: url('../media/type/Gotham-Book.woff2') format('woff2'),
	url('../media/type/Gotham-Book.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Roboto';
	src: url('../media/type/Roboto-Bold.woff2') format('woff2'),
	url('../media/type/Roboto-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Roboto';
	src: url('../media/type/Roboto-Regular.woff2') format('woff2'),
	url('../media/type/Roboto-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Gotham';
	src: url('../media/type/Gotham-Bold.woff2') format('woff2'),
	url('../media/type/Gotham-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: 'American Typewriter';
	src: url('../media/type/AmericanTypewriter.woff2') format('woff2'),
	url('../media/type/AmericanTypewriter.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@mixin font($ff: 'Gotham', $fw: $fwRegular, $fsi: normal) {
	font-weight: $fw;
	font-style: $fsi;
	font-family:  $ff;
	@if $ff == Gotham {
		font-family: $fontFamily__default;
	} @else if $ff == Roboto {
		font-family: $fontFamily__roboto;
	} @else if $ff == Serif {
		font-family: $fontFamily__serif;
	} @else {
		@error "Unknown font #{$ff}.";
	}
}