// <>
body {
	overflow-x: hidden;
}

// plugins
.swiper-button-next,
.swiper-button-prev {
	width: 42px !important;
	height: 68px !important;
	background-color: transparent !important;
	z-index: 10;
}
.swiper-button-prev {
	background-image: url(#{$media_assets}swiper_left.svg) !important;
	left: 0 !important;
}
.swiper-button-next {
	background-image: url(#{$media_assets}swiper_right.svg) !important;
	right: 0 !important;
}
.swiper-pagination {
	margin-top: 20px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	position: relative;
	bottom: 0;
	height: 10px;
	.swiper-pagination-bullet {
		@include opacity(0.1);
		@include roundedAll(50%);
		display: block;
		width: 10px;
		height: 10px;
	}
	.swiper-pagination-bullet-active {
		@include opacity(1);
		background: $primary;
	}
}

// l-
.l-nav {
	@include transition($ease: ease-in);
	display: block;
	width: 100%;
	max-width: $maxRowWidth;
	margin: 0 auto;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 12;
	&__primary {
		background: $blue;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		min-height: 40px;
		padding: 0 20px;
		span, li {
			@include font(Gotham, bold);
			font-size: 11px;
			color: $text;
			text-transform: uppercase;
		}
		ul {
			display: flex;
			flex-direction: row;
			align-items: center;
			flex-wrap: wrap;
			justify-content: center;
			li {
				margin-right: 30px;
				&:last-of-type {
					margin-right: 0;
				}
			}
		}
	}

	&__secondary {
		@include transition();
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		position: relative;
		padding: 35px 0 45px 0;
		.u-containerFlex {
			position: relative;
			justify-content: center;
			align-items: center;
		}
	}
	&__search {
		@include flexWidth(220px);
		position: absolute;
		left: 0;
	}
	&__brand {
		@include flexWidth(200px);
		display: block;
		a, img {
			display: block;
			width: 100%;
		}
	}
	&__userControl {
		@include flexWidth(180px);
		position: absolute;
		right: 0;
		display: flex;
		flex-direction: row;
		align-items: stretch;
		> a {
			@include transition();
			display: flex;
			flex-direction: row;
			align-items: center;
		}
		.userControl__signUp {
			@include font(Gotham, Bold);
			width: 100px;
			height: 38px;
			padding: 0 10px;
			color: $white;
			font-size: 10px;
			line-height: 11px;
			text-transform: uppercase;
			background: $secondary;
			&:hover {
				background: $secondaryDark;
			}
		}
		.userControl__cart {
			@include transition();
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			padding: 0 15px;
			width: 80px;
			height: 38px;
			background: $yellow;
			position: relative;
			img {
				@include transition();
				display: block;
				width: 16px;
				height: 15px;
				object-fit: contain;
			}
			.cart_1 {
				@include opacity(0.5);
			}
			.cart_2 {
				width: 10px;
			}
			&:hover {
				background: $yellowDark;
				img {
					@include opacity(1);
				}
			}
			&.is-active {
				background: $yellowDark;
				.cart_2 {
					@include transforms(rotate(180deg), center);
				}
			}
		}
	}

	&__third {
		display: block;
		position: relative;
	}
	&__contentHero {
		display: block;
		width: 100%;
		position: relative;
		z-index: 3;
	}
	&__menu {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		> li {
			display: block;
			position: relative;
			margin-right: 15px;
			&:last-of-type {
				margin-right: 0;
			}
		}
		.c-btn {
			padding: 0 20px;
			min-width: 70px;
		}
	}

	&__maskMenu {
		@include u-hideElement();
		z-index: 2;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		min-height: 100vh;
		display: block;
		background: rgba(0,0,0,0.5);
		&.is-active {
			@include u-showElement();
		}
	}
	
	.c-hamburguer {
		@include transforms(translateY(-50%), center);
		display: none;
		position: absolute;
		right: 15px;
		top: 50%;
		z-index: 4;
		.c-hamburguer__hero {
			@include transforms($property: rotate(0deg));
			@include transition();
			cursor: pointer;
			display: block;
			height: 26px;
			position: relative;
			width: 30px;
			.bar {
				@extend .u-showElement;
				@include roundedAll(1px);
				@include transforms($property: rotate(0deg));
				@include transition();
				background: $primary;
				display: block;
				height: 2px;
				position: absolute;
				width: 100%;
				&.bar_1 {
					top: 0;
					left: 0;
				}
				&.bar_2 {
					top: 12px;
					left: 0;
				}
				&.bar_3 {
					top: 24px;
					left: 0;
				}
			}
		}
		&.is-active {
			background: $primaryLight;
			margin-top: -10px;
			right: 15px;
			@include roundedAll(20px);
			.c-hamburguer__hero {
				@include transforms($property: rotate(180deg) scale(0.7));
				.bar {
					background: $white;
					&.bar_1 {
						top: 12px;
						@include transforms($property: rotate(45deg));
					}
					&.bar_2 {
						@extend .u-hideElement;
					}
					&.bar_3 {
						top: 12px;
						@include transforms($property: rotate(-45deg));
					}
				}
			}
		}
	}
	
	&.hidden {
		top: -300px;
	}
	&.scrolled {
		background: $white;
		.l-nav__secondary {
			padding-top: 15px;
			padding-bottom: 20px;
		}
		.l-nav__third {
			top: 10px;
		}
		.c-hamburguer {
			&.is-active {
				margin-top: 5px;
			}
		}
	}
}
.l-footer {
	background: $primary;
	padding: 80px 0 95px 0;
	.u-containerFlex {
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
		@include calcWidthForPositionNegative(-15px);
		> * {
			padding: 15px;
		}
	}
	&__brand {
		@include flexWidth(200px);
		@include opacity(0.5);
		a, img {
			display: block;
			width: 100%;
		}
	}
	&__menu {
		@include flexWidth(220px);
		padding-top: 15px;
		a {
			@include font(Roboto, normal);
			font-size: 11px;
			color: $text;
			text-transform: uppercase;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	&__PSmethods {
		@include flexWidth(245px);
		align-self: flex-end;
		.img__blendMode {
			@include opacity(1);
			background-image: url(#{$media_assets}paymentMethods.jpg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			background-color: $primary;
			background-blend-mode: multiply;
			display: block;
			width: 100%;
			height: 42px;
		}
	}
	&__newsletter {
		@include flexWidth(260px);
		h4 {
			@include font(Roboto, normal);
			font-size: 13px;
			color: $text;
			text-transform: uppercase;
			strong {
				font-weight: $fwMedium;
			}
		}
		p {
			@include font(Roboto, normal);
			font-size: 11px;
			color: $text;
			margin-top: 5px;
			margin-bottom: 10px;
		}
		.footer-news-form {
			width: 100%;
			display: block;
			position: relative;
			input {
				display: block;
				width: 100%;
				height: 40px;
				line-height: 40px;
				padding: 0 50px 0 20px;
				@include font(Roboto, normal);
				font-size: 11px;
				color: $text;
				@include placeholder {
					color: $text;
				}
			}
			button {
				@include transition();
				width: 40px;
				height: 40px;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: row;
				position: absolute;
				right: 0;
				top: 0;
				background: transparent;
				cursor: pointer;
				&:hover {
					background: $white2;
				}
			}
		}
	}
}
.l-headerHome {
	padding-top: 250px;
	display: block;
	position: relative;
	&__figureOne,
	&__figureTwo {
		@include flexWidth(50%);
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		position: relative;
		img {
			display: block;
			width: 100%;
			max-width: 580px;
			position: relative;
		}
		&:before {
			content: '';
			display: block;
			position: absolute;
		}
	}
	&__figureOne {
		z-index: 2;
		justify-content: flex-end;
		&:before {
			width: 90%;
			height: 96%;
			top: 0;
			left: 0;
			background: $secondary;
		}
		img {
			top: -55px;
			left: 60px;
		}
	}
	&__figureTwo {
		z-index: 1;
		justify-content: flex-start;
		&:before {
			width: 100%;
			height: 80%;
			top: 10%;
			right: -30px;
			background: $yellow;
		}
	}
	&__content {
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		z-index: 4;
		width: 100%;
		padding: 30px 15px 0 15px;
		h1 {
			@include font(Gotham, bold);
			color: $white;
			font-size: 58px;
			line-height: 1;
			text-align: center;
			display: block;
			width: 100%;
		}
		.c-btn {
			text-transform: none;
			margin: -5px auto 0 auto;
		}
	}
}
.l-headerInner {
	display: block;
	background: $primaryLight;
	padding: 100px 0;
	h1 {
		color: $secondary;
	}
}
.l-innerPage {
	display: block;
	padding-top: 250px;
	padding-bottom: 80px;
}
.l-copyright {
	padding: 45px 42px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	&__legal {
		flex: 1 1 auto;
		padding-right: 50px;
		p {
			@include font(Roboto, normal);
			font-size: 11px;
			color: $text;
		}
	}
	&__brand {
		@include flexWidth(145px);
		a, img {
			display: block;
			width: 100%;
		}
	}
}

// s-
.s-cart {
	@include transition();
	@include u-hideElement;
	position: absolute;
	top: 58px;
	right: 0;
	z-index: 12;
	display: block;
	width: 440px;
	padding: 20px;
	background: $white3;
	&.is-active {
		@include u-showElement;
		top: 38px;
	}
	ul {
		li {
			display: block;
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid $lightGray;
		}
	}
	.finalPrice {
		@include font(Gotham, bold);
		color: $primary;
		font-size: 20px;
		display: block;
		width: 100%;
		text-align: center;
		margin-bottom: 15px;
		padding-bottom: 15px;
		border-bottom: 1px solid $lightGray;
	}
	.c-btn {
		max-width: 100%;
		text-align: center;
	}
}
.s-instagram {
	.u-container {
		padding: 50px 0;
	}
	h3 {
		@include letterSpacing(50);
		@include font(Gotham, bold);
		font-size: 34px;
		color: $secondary;
		text-align: center;
		margin-bottom: 20px;
		span {
			@include letterSpacing(50);
			@include font(Gotham, $fwMedium);
			font-size: 34px;
			color: $secondary;
			text-align: center;
		}
	}
	.c-btn {
		text-transform: none;
		font-style: italic;
		margin: 0 auto;
	}
	&__grid {
		@include calcWidthForPositionNegative(-2px);
		background: $white;
		padding: 2px 0;
		li {
			padding: 2px;
		}
		a {
			@extend .u-aspectRatio;
			@extend .u-aspectRatio--1-1;
		}
	}
}
.s-intro {
	padding: 80px 0;
	.u-container {
		max-width: 950px;
	}
	h2 {
		@include font(Gotham, bold);
		font-style: italic;
		color: $primary;
		font-size: 34px;
		line-height: 1.1;
		text-align: center;
	}
}
.s-categories {
	display: block;
	position: relative;
	.swiper__categories {
		overflow: hidden;
	}
	.swiper-slide {
		a {
			@extend .u-aspectRatio;
			padding-top: 138%;
			img {
				@include transition();
			}
			&:hover {
				img {
					@include transforms(scale(1.1), center);
				}
			}
		}
	}
}
.s-styleProfile {
	padding-top: 100px;
	display: block;
	&__banner {
		display: flex;
		flex-direction: row;
		align-items: center;
		width: 100%;
		height: 310px;
		overflow: hidden;
		> img {
			@include transition();
			display: block;
			width: 100%;
			height: 100%;
			z-index: 1;
			object-fit: cover;
		}
		&:hover {
			> img {
				@include transforms(scale(1.05), center);
			}
		}
	}
	.banner__text {
		padding-left: 100px;
		display: block;
		position: absolute;
		left: 0;
		z-index: 2;
	}
	h2 {
		@include font(Serif, bold, italic);
		font-size: 66px;
		display: block;
		position: relative;
		margin-bottom: 10px;
		img {
			display: block;
			width: 100%;
			max-width: 380px;
			position: relative;
			z-index: 2;
			left: -20px;
		}
	}
	p {
		@include font(Serif, normal, normal);
		font-size: 28px;
		color: $white;
		max-width: 200px;
	}
}
.s-breadcrumb {
	ul {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		li {
			margin-right: 12px;
			display: block;
			position: relative;
			&:after {
				@include font(Gotham, bold);
				content: ':';
				display: block;
				color: $yellow;
				font-size: 11px;
				position: absolute;
				right: -8px;
				top: -1px;
			}
			&:last-of-type {
				margin-right: 0;
				&:after {
					display: none;
				}
			}
		}
		a {
			@include font(Gotham, bold);
			display: block;
			text-transform: uppercase;
			color: $yellow;
			font-size: 11px;
			&:hover {
				text-decoration: underline;
			}
			&.is-active {
				color: $secondary;
				pointer-events: none;
			}
		}
	}
}
.s-onlyText {
	p {
		@include paragrafos();
	}
	ol, ul {
		margin-bottom: 20px;
		li {
			display: list-item;
			list-style-position: inside;
			@include paragrafos($mb: 10px);
		}
	}
	ol {
		li {
			list-style-type: decimal-leading-zero;
		}
	}
	ul {
		li {
			list-style-type: disc;
		}
	}
	h1, h2, h3, h4, h5, h6 {
		margin-bottom: 10px;
	}
}
.s-departmentIntro {
	padding: 20px 0;
	h1 {
		@include font(Gotham, bold);
		font-size: 40px;
		text-transform: uppercase;
		color: $primary;
		margin-bottom: 20px;
	}
	.swiper__departamentos {
		@include calcWidthForPositionNegative(-10px);
	}
	.swiper-slide {
		padding: 10px;
	}
}
.s-departmentProducts {
	&__filters {
		.filter {
			flex: 0 1 auto;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-start;
			margin-right: 20px;
			position: relative;
			&:last-of-type {
				margin-right: 0;
			}
			.label {
				@include font(Gotham, bold);
				text-transform: uppercase;
				display: block;
				color: $text;
				font-size: 11px;
				white-space: nowrap;
				padding-right: 15px;
			}
			.fakeSelect {
				@include font(Gotham, bold);
				@include transition();
				white-space: nowrap;
				color: $text;
				font-size: 11px;
				text-transform: uppercase;
				min-width: 90px;
				height: 30px;
				line-height: 30px;
				display: block;
				position: relative;
				padding: 0 50px 0 20px;
				background: $white3;
				overflow: hidden;
				text-overflow: ellipsis;
				&:after {
					@include transition();
					content: '';
					display: block;
					width: 10px;
					height: 30px;
					position: absolute;
					right: 20px;
					top: 0;
					background: url(#{$media_assets}caret-down.svg);
					background-position: center;
					background-repeat: no-repeat;
					background-size: contain;
				}
				&:hover {
					background: $lightGray;
				}
			}
			&.--order {
				flex: 1 1 auto;
				justify-content: flex-end;
			}
			&.is-active {
				.fakeSelect {
					background: $yellow;
					&:after {
						@include transforms(rotate(180deg), center);
					}
				}
			}
		}

		.u-subMenu {
			@include u-hideElement;
			@include transition;
			position: absolute;
			top: 50px;
			padding: 10px;
			right: 0;
			max-width: 140px;
			min-width: 90px;
			width: 100%;
			background: $yellowLight;
			z-index: 2;
		}
		.u-subMenu.is-active {
			@include u-showElement;
			top: 30px;
			a {
				@include font(Gotham, bold);
				font-size: 12px;
				display: block;
				width: 100%;
				text-transform: uppercase;
				color: $text;
				padding: 5px 10px;
				&:hover {
					background: $yellow;
				}
				&.is-active {
					color: $text;
					background: $yellowDark;
				}
			}
		}
	}
	&__grid {
		margin-top: 20px;
		ul {
			@include calcWidthForPositionNegative(-20px);
			li {
				padding: 20px;
				margin-bottom: 50px;
			}
		}
		.btnMoreProducts {
			@include opacity(0.4);
			max-width: 550px;
			border-color: rgba(0,0,0,0.5) !important;
			color: $text !important;
			height: 110px !important;
			line-height: 110px !important;
			margin: 0 auto;
			&:hover {
				@include opacity(1);
				border-color: $primary !important;
				color: $white !important;
			}
		}
	}
}
.s-mainProduct {
	display: block;
	padding-top: 40px;
	&__media {
		flex: 1 1 auto;
		display: flex;
		flex-direction: row;
		align-items: stretch;
		justify-content: space-between;
		flex-wrap: nowrap;
		position: relative;
		.media__text {
			@include flexWidth(70px);
			@include transforms(rotate(180deg), center);
			@include font(Gotham, bold);
			writing-mode: vertical-rl;
			display: flex;
			align-items: center;
			justify-content: center;
			white-space: nowrap;
			text-align: center;
			font-size: 25px;
			color: $secondary;
		}

		.media__pic,
		.media__video {
			flex: 1 1 50%;
			a {
				@extend .u-aspectRatio;
				padding-top: 138%;
				img {
					@include transition();
				}
				&:hover {
					img {
						@include transforms(scale(1.1), center);
					}
				}
			}
		}
		.media__video {
			a {
				&:after {
					@include transition();
					content: '';
					display: block;
					width: 110px;
					height: 110px;
					position: absolute;
					top: 50%;
					left: 50%;
					margin-top: -55px;
					margin-left: -55px;
					background-image: url(#{$media_icons}icon_play.png) !important;
					background-repeat: no-repeat;
					background-position: center;
				}
				&:hover {
					&:after {
						@include opacity(0.85);
					}
				}
			}
		}
	}
	&__infos {
		@include flexWidth(395px);
		padding-left: 60px;
		.c-cardProduct {
			position: sticky;
		}
	}
}
.s-textProduct {
	padding: 60px 0;
}
.s-buyMore {
	padding-bottom: 40px;
	.u-containerBig {
		> h1 {
			@include font(Gotham, bold);
			margin-bottom: 30px;
			display: block;
			color: $primary;
			font-size: 40px;
		}
	}
	ul {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		li {
			flex: 1 1 auto;
			position: relative;
		}
		li.final {
			top: -120px;
		}
		li.spacer {
			@include flexWidth(140px);
			top: -120px;
			span {
				@include font(Gotham, bold);
				display: block;
				text-align: center;
				color: $primary;
				font-size: 100px;
			}
		}
		li.card {
			@include flexWidth(250px);
			.c-cardProduct {
				h1 {
					font-size: 28px;
				}
				.infos__resume {
					.price__old {
						font-size: 15px;
					}
					.price__now {
						font-size: 24px;
					}
				}
			}
		}
	}
	.c-cardProduct {
		&__info {
			padding: 0;
		}
	}
}
.s-buyFullCover {
	&__hero {
		display: block;
		position: relative;
		img {
			display: block;
			width: 100%;
		}
		.fixedBuy {
			position: absolute;
			top: 0;
			right: 0;
			display: block;
			width: 100%;
			height: 100%;
			max-width: 470px;
		}
	}
	.c-cardProduct {
		background: rgba(255,255,255,0.9);
		padding: 60px;
		position: sticky;
		.infos__resume {
			padding-top: 0;
		}
	}
}
.s-lookMore {
	padding-top: 60px;
	.u-containerBig {
		> h1 {
			@include font(Gotham, bold);
			margin-bottom: 20px;
			display: block;
			color: $primary;
			font-size: 40px;
		}
	}
	ul {
		@include calcWidthForPositionNegative(-20px);
		li {
			padding: 20px;
			margin-bottom: 50px;
		}
	}
}

// #page
#page__components {
	padding: 60px 50px;
	.gridComponent {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
	}
	.component {
		@include roundedAll(10px);
		flex: 1 1 auto;
		max-width: 380px;
		min-width: 280px;
		width: 100%;
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		margin: 10px;
		background: teal;
		.component__header {
			padding: 15px;
			> h2 {
				color: $white;
				font-size: 18px;
			}
		}
		.component__content {
			@include roundedAll(10px);
			display: block;
			padding: 15px;
			background: $white;
			border: 3px solid teal;
			flex: 1 1 auto;
		}
		.content__hero {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid $mutedGray;
			&:last-of-type {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
			> h3 {
				color: $baseGray;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 13px;
				margin-bottom: 4px;
			}
			.obs {
				font-size: 14px;
				color: $text;
				display: block;
				width: 100%;
				margin-top: 5px;
				padding-top: 5px;
				border-top: 1px solid $mutedGray;
				strong {
					font-weight: bold;
				}
				u {
					text-decoration: underline;
				}
				i {
					font-style: italic;
				}
			}
		}
	}
	> section {
		margin-bottom: 40px;
		padding-bottom: 40px;
		border-bottom: 1px solid $mutedGray;
		&:last-of-type {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: 0;
		}
		> h1 {
			color: $primary;
			font-size: 24px;
			text-align: center;
			font-weight: bold;
			margin-bottom: 20px;
		}
	}
}
#page__erro404 {
	display: flex;
	align-items: center;
	.u-container {
		text-align: center;
	}
	h2 {
		text-transform: uppercase;
		line-height: 1;
		margin-bottom: 50px;
		span {
			display: block;
			text-align: center;
		}
		.erro_caption {
			font-size: 50px;
		}
		.erro_code {
			font-size: 100px;
		}
	}
	h3 {
		padding: 30px 0;
		font-size: 50px;
		line-height: 1;
	}
	a {
		color: $white;
		&:hover {
			text-decoration: underline;
		}
	}
}
