// ie detect
#w_ieDetect {
    position: fixed;
    display: block !important;
    padding: 15px 15px;
    bottom: -500px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 100%;
    max-width: 400px;
    background: $white;
    border-bottom: 1px solid $lightGray;
    overflow: hidden;
    z-index: 100;
    @include transition();
    @include roundedAll(5px);
    
    &.is-visible {
        bottom: 50%;
        @include transforms(translateY(50%), center);
    }
    
    &.is-fixed {
        bottom: 20px;
        .w_ieDetect__listBrowsers, .w_ieDetect__unlockSite, p {
            display: none;
        }
        &:hover {
            p, .w_ieDetect__unlockSite {
                display: block;
            }
            .w_ieDetect__listBrowsers {
                display: flex;
            }
        }
        
    }
    
    i {
        display: block;
        margin: 0 auto;
    }
    svg {
        display: block;
        width: 68px;
        height: 68px;
        margin: 0 auto;
    }
    p {
        color: #666;
        font-size: 16px;
        line-height: 1.4;
        display: block;
        text-align: center;
        margin-bottom: 12px;
        strong {
            font-weight: bold;
        }
    }
    .w_ieDetect__listBrowsers {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        a {
            flex: 0 1 50%;
            @include calc(max-width, 50%, '-', 4px);
            margin: 4px 0;
            cursor: pointer;
            color: #222;
            display: block;
            @include roundedAll(5px);
            padding: 10px 0px;
            background: #ededed;
            font-size: 14px;
            line-height: 1;
            float: left;
            //width: 125px;
            text-align: center;
            &:hover {
                background: #1ca33d;
                color: #fff;
            }
            strong {
                font-weight: bold;
            }
        }
    }
    .w_ieDetect__unlockSite {
        display: block;
        width: 100%;
        text-align: center;
        color: #555;
        font-size: 14px;
        line-height: 1.2;
        margin: 15px auto 0 auto;
        padding-top: 15px;
        max-width: 260px;
        border-top: 1px solid #ccc;
        cursor: pointer;
        &:hover {
            text-decoration: underline;
            color: $darkGray;
        }
    }
}
#w_ieDetect__mask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    display: none;
    background: rgba(40,40,40,0);
    z-index: 99;
    &.is-visible {
        display: block;
        background: rgba(40,40,40,0.9);
    }
}