// .c-tabHorizontal
@media screen and (max-width: $containerWidth) {
	.c-tabHorizontal {
		width: 100%;
	}
}
@media screen and (max-width: 700px) {
	.c-tabHorizontal__header a {
		font-size: 30px;
	}
	.c-tabHorizontal__header li {
		padding-right: 36px;
		&:after {
			right: 18px;
		}
	}
}